import React, {Component} from "react";
import {connect} from "react-redux";
import ProductGalleryMiniPicturesComponent from "eshop/modules/simfree/components/gallery/ProductGalleryMiniPicturesComponent";
import ProductGalleryLargePictureComponent from "eshop/modules/simfree/components/gallery/ProductGalleryLargePictureComponent";
import {getSelectedOfferType} from "eshop/modules/configurator/selectors/filters"
import {getSelectedOfferId} from "eshop/modules/configurator/selectors/offers";

class ProductGalleryComponentView extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="l-row opl-sticked-element">
                <div className="l-col l-col-small-12 l-col-medium-2 l-col-2  u-small-hide">
                    <ProductGalleryMiniPicturesComponent productVariant={this.props.productVariant}/>
                </div>
                <div className="l-col l-col-small-12 l-col-medium-10 l-col-10" id="main-picture-container">
                    <ProductGalleryLargePictureComponent productVariant={this.props.productVariant}
                                                         sticker={this.props.sticker !== null && (this.props.sticker.propositionItems && this.props.sticker.propositionItems.length === 0 ||
                                                                this.props.sticker.propositionItems.includes(this.props.selectedOfferId))
                                                             ? this.props.sticker : null}
                                                         selectedOfferType={this.props.selectedOfferType} />
                </div>
            </div>
        )
    }

}

const mapStateToProps = state => ({
    selectedOfferType:getSelectedOfferType(state),
    selectedOfferId:getSelectedOfferId(state)
});

const ProductGalleryComponent = connect(mapStateToProps)(ProductGalleryComponentView);
export default ProductGalleryComponent;